body {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  min-height: 100vh;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #333333;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.advantages {
  background-color: #ffffff;
}

.advantages__intro {
  margin: 0 auto;
}

.call-to-action {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 38px 18px 49px;
  text-transform: uppercase;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .call-to-action {
    padding: 61px 18px 58px;
  }
}

.call-to-action__title {
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  margin: 0 0 23px 0;
}
@media (min-width: 768px) {
  .call-to-action__title {
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 26px;
    margin: 0 0 32px;
  }
}

.call-to-action__message {
  max-width: 458px;
  text-align: center;
  margin: 0 0 31px 0;
}
@media (min-width: 768px) {
  .call-to-action__message {
    margin: 0 0 41px;
  }
}
@media (min-width: 1200px) {
  .call-to-action__message {
    margin: 0 0 45px;
  }
}

.call-to-action__break {
  display: none;
}
@media (min-width: 768px) {
  .call-to-action__break {
    display: inline;
  }
}

.call-to-action__button {
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 58px;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
  outline: none;
  max-width: 458px;
}
.call-to-action__button:hover {
  background-color: #669ec0;
  transition: 0.2s ease-out 0s;
}
.call-to-action__button:active {
  color: #87b6d1;
  background-color: #5496bd;
}
@media (min-width: 1200px) {
  .call-to-action__button {
    margin: 0 0 8px;
  }
}

.checkbox {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: none;
  cursor: pointer;
}
.checkbox:not(:last-child) {
  margin-bottom: 16px;
}
.checkbox:active .checkbox__input {
  background-color: #d5d5d5;
}
.checkbox:active .checkbox__input:checked::before {
  background-image: url("../img/tick.svg#active");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .checkbox:last-child {
    margin-bottom: 16px;
  }
}
@media (min-width: 1200px) {
  .checkbox {
    justify-content: center;
  }
  .checkbox:nth-last-child(2) {
    margin-left: -10px;
  }
  .checkbox:last-child {
    margin-left: -12px;
  }
}

.checkbox__input {
  position: relative;
  width: 46px;
  height: 46px;
  margin-right: 16px;
  background-color: #f2f2f2;
  border-radius: 4px;
  appearance: none;
  cursor: pointer;
}
.checkbox__input::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 22px;
  height: 16px;
  background-image: url("../img/tick.svg#base");
  background-repeat: no-repeat;
}
.checkbox__input:checked::before {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.2s ease-out 0s;
}
.checkbox__input:hover {
  background-color: #e6e6e6;
  transition: 0.2s ease-out 0s;
}

.created {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;
  color: #000000;
  text-transform: uppercase;
  transition: 0.2s ease-out 0s;
}
@media (min-width: 768px) {
  .created {
    justify-content: flex-end;
  }
}
.created:hover {
  color: #81b3d2;
}
.created:active {
  color: #b3b3b3;
}

.created__text {
  margin-right: 13px;
}

.feature {
  text-align: center;
  text-transform: uppercase;
  background-color: #eeeeee;
}
.feature--extended {
  background-color: #81b3d2;
  color: #ffffff;
}
.feature:last-child {
  padding: 0 0 5px;
}
@media (min-width: 768px) {
  .feature:nth-child(1) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 256px 330px;
  }
  .feature:nth-child(1) .feature__wrapper {
    grid-column: 1/span 1;
  }
  .feature:nth-child(1) .feature__image {
    grid-column: 2/span 2;
  }
  .feature:nth-child(1) .feature__services {
    grid-column: 1/span 3;
    grid-row: 2/span 1;
  }
  .feature:nth-child(2) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .feature:nth-child(2) .feature__wrapper {
    grid-column: 3/span 1;
    grid-row: 1/span 1;
  }
  .feature:nth-child(2) .feature__image {
    grid-column: 1/span 2;
    grid-row: 1/span 1;
  }
}

.feature__image img {
  height: 260px;
  object-fit: cover;
}
@media (min-width: 321px) {
  .feature__image img {
    width: 512px;
    height: 256px;
  }
}
@media (min-width: 513px) {
  .feature__image img {
    width: 800px;
    height: 256px;
  }
}

.feature__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 18px;
}
.feature__wrapper--city {
  padding-top: 70px;
  padding-bottom: 40px;
}
.feature__wrapper--bridge {
  padding-top: 37px;
  padding-bottom: 37px;
}
@media (min-width: 768px) {
  .feature__wrapper {
    padding-top: 52px;
  }
  .feature__wrapper--middle {
    padding: 41px 30px 0;
  }
}
@media (min-width: 1200px) {
  .feature__wrapper {
    padding: 62px 0 0;
  }
  .feature__wrapper--city, .feature__wrapper--bridge {
    padding: 52px 0 0;
  }
  .feature__wrapper--middle {
    padding: 52px 30px 0;
  }
}

.feature:nth-last-child(-n+2) .feature__wrapper {
  border-top: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .feature:nth-last-child(-n+2) .feature__wrapper {
    border: none;
  }
}

.feature__title {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0 0 11px 0;
}
@media (min-width: 768px) {
  .feature__title {
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    width: 150px;
    margin-bottom: 14px;
  }
}

.feature__number {
  margin: 0 0 17px 0;
}
@media (min-width: 768px) {
  .feature__number {
    margin-bottom: 22px;
  }
}

.feature__description {
  margin: 0;
  padding: 0 15px;
}
.feature__description--extended {
  padding: 0 5px;
}
@media (min-width: 768px) {
  .feature__description {
    padding: 0 15px;
  }
}

.feature__break {
  display: none;
}
@media (min-width: 1200px) {
  .feature__break {
    display: inline;
  }
}

.feedback {
  background-color: #ffffff;
}

.feedback__intro {
  margin: 0 auto;
}

.fieldset {
  position: relative;
  margin: 0;
  text-transform: uppercase;
  border: none;
}
.fieldset:nth-child(1) {
  padding: 24px 0 39px;
}
.fieldset:nth-child(2) {
  padding: 24px 0 34px;
}
.fieldset:nth-child(3) {
  padding: 27px 0 24px;
}
.fieldset:nth-child(4) {
  padding: 27px 0 23px;
}
.fieldset:nth-child(5) {
  padding: 24px 0 0;
}
@media (min-width: 768px) {
  .fieldset:nth-child(1) {
    margin: 0 0 61px;
    padding: 54px 43px 14px 0;
    border-right: 1px solid #e5e5e5;
  }
  .fieldset:nth-child(2) {
    padding: 54px 0 0 43px;
  }
  .fieldset:nth-child(3) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 40px 28px 74px;
  }
  .fieldset:nth-child(4) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    padding: 55px 84px 38px;
  }
  .fieldset:last-child {
    padding: 40px 0 64px;
  }
  .fieldset:nth-child(n+3) {
    border-top: 1px solid #e5e5e5;
  }
  .fieldset:nth-child(n+3) > .fieldset__title {
    position: absolute;
    top: 0;
    left: 50%;
    width: 400px;
    transform: translate(-50%, -50%);
  }
  .fieldset:nth-child(-n+2) > .fieldset__title {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
@media (min-width: 1200px) {
  .fieldset:nth-child(1) {
    margin: 0 0 69px;
    padding: 54px 62px 14px 0;
  }
  .fieldset:nth-child(2) {
    padding: 54px 0 0 65px;
  }
  .fieldset:nth-child(3) {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 50px;
    row-gap: 26px;
    padding: 58px 46px 98px;
  }
  .fieldset:nth-child(4) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 0;
    padding: 85px 14px 84px;
  }
}
.fieldset::before {
  position: absolute;
  content: "";
  top: 0;
  left: -20px;
  height: 1px;
  width: 100vw;
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .fieldset::before {
    content: none;
  }
}

.fieldset__title {
  text-align: center;
  margin: 0 0 20px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
@media (min-width: 768px) {
  .fieldset__title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    background-color: #ffffff;
  }
}

.fieldset__textarea {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  display: block;
  width: 100%;
  min-height: 150px;
  margin-bottom: 56px;
  padding: 16px;
  color: #000000;
  background-color: #f2f2f2;
  border: none;
  outline: none;
  overflow: hidden;
  text-overflow: ellipsis;
  resize: unset;
}
.fieldset__textarea::placeholder {
  color: #a9a9a9;
}
.fieldset__textarea:hover {
  background-color: #e6e6e6;
}
.fieldset__textarea:focus {
  outline: 4px solid #e6e6e6;
  outline-offset: -4px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .fieldset__textarea {
    height: 160px;
    margin-bottom: 48px;
  }
}
@media (min-width: 1200px) {
  .fieldset__textarea {
    width: 768px;
    margin: 0 auto 48px;
  }
}

.fieldset__submit-group {
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .fieldset__submit-group {
    width: 768px;
    margin: 0 auto;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 39px 0 23px;
  background-color: #ffffff;
