.gallery {
  padding: 0 20px;
  overflow: hidden;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .gallery {
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .gallery {
    padding: 0;
  }
}

.gallery__intro {
  margin: 0 auto;
}

.hero {
  position: relative;
  height: 158px;
  background-image: url("../img/hero-bg-tablet.webp");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #6c869b;
  overflow-x: hidden;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-bg-tablet@2x.webp");
  }
}
@media (min-width: 768px) {
  .hero {
    height: 290px;
  }
}
@media (min-width: 1200px) {
  .hero {
    height: 301px;
    background-image: url("../img/hero-bg-desktop.webp");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-bg-desktop@2x.webp");
  }
}
.hero::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 1200px;
  height: 57px;
  background-image: url("../img/hero-triangle.svg");
  background-position: bottom;
